.header {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-around;
    height: 15vh;
    background-color: #271a36de;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
    top: 0;
    position: fixed;
    width: 100%;
    z-index: 999;
}

.title {
    font-family: cursive;
    color: white;
    font-size: 6vh;
    text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
}

.title::first-letter {
    color: #26e897;
    font-size: 2em;
}

.brand {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1vh;
    height: inherit;
}

.logo {
    filter: invert();
    
    height: inherit;
}

@media (width < 720px){
    .header {
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        height: 15vh;
    }

    .brand {
        width: 100vw;
        justify-content: center;
    }

    .title {
        font-size: 6vw;
    }

    .logo {
        height: 10vh;
    }

}