* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* > h1 {
  font-family: cursive;
  font-size: 5vh;
}

* > p {
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 3vh;
}

.body {
  background-color: #f2f2f2;
}

header {
  height: 15vh;
  background-color: #271a36;
}

main {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 75vh;
  overflow-x: hidden;
}
 