.navbar {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    list-style: none;
    font-family: cursive;
    font-size: 2vh;
}

.item {
    transition-duration: .5s;
    text-decoration: none;
    color: white;
    font-weight: bold;
    padding-bottom: 2vh;
    border-bottom: 3px solid transparent;
}

.item:hover {
    color: #26e897;
    border-color: #26e897;
}
