.tile {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100vh;
    width: 100vw;
    background-image: radial-gradient(transparent
    , transparent, #271a36), url("../images/intro.jpg");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.intro {
    display: block;
    color: black;
    backdrop-filter: blur(8px);
    margin-inline: 5vw;
    padding: 1vw;
    background-color: rgba(255, 255, 255, 0.3);
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
    border-radius: 30px;
}

.intro > h1 {
    color: #271a36;
    border-radius: 50%;
}

.intro > p {
    padding: 1vh;
    border-left: 1vh solid #2D728F;
    border-right: 1vh solid #2D728F;
    border-radius: 2vh;
}

.logo {
    height: 40vh;
    margin: -7vh;
}