.tile {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50vh;
    font-family: 'Times New Roman', Times, serif;
    font-size: 5vh;
    color: #542344;
    font-weight: 200;
    background-image: radial-gradient(black transparent);
}

.tile > h2 {
    text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
}
